.container
  width: 320px
  padding: 0 20px
  margin: 0 auto

  @media ( min-width: 768px )
    width: 768px
    padding: 0 30px

  @media ( min-width: 1200px )
    width: 1200px
    padding: 0 60px

.btn
  width: 280px
  height: 40px
  border: none
  font-style: normal
  font-weight: 400
  font-size: 14px
  line-height: 18px
  text-align: center
  text-transform: uppercase
  color: #FFFFFF
  background-color: #68B738
  cursor: pointer
