@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald Medium";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Oswald Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
html {
  height: 100%;
}

body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

ul {
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 320px;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 60px;
  }
}

.btn {
  width: 280px;
  height: 40px;
  border: none;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #68B738;
  cursor: pointer;
}

.header {
  width: 100%;
  height: 66px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    height: 90px;
  }
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 11px;
}
@media (min-width: 768px) {
  .header__inner {
    padding-top: 24px;
  }
}

.header__logo-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 191px;
}
@media (min-width: 768px) {
  .header__logo-group {
    width: 174px;
  }
}

.header__logo-img {
  width: 100%;
}

@media (min-width: 768px) {
  .header__logo-text {
    display: none;
  }
}

.navbar-wrapper {
  display: none;
}
@media (min-width: 768px) {
  .navbar-wrapper {
    display: block;
  }
}

.navbar__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 440px;
}

.navbar__link {
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.25s all;
}
.navbar__link:hover {
  opacity: 0.6;
}

@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.intro {
  width: 100%;
  background-image: url("../img/index-background-mobile@1px.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: #FFFFFF;
}
.intro__title {
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: #FFFFFF;
}
.intro__subtitle {
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  color: #FFFFFF;
}